/* ----------------------- */
body {
  font-family: "Josefin Sans", sans-serif;
}
/* Start landing page */
.landingPageWrapper {
  height: 100vh;
  /* background-image: url("./assets/images/background.jpg"); */
  position: relative;
}

.landingBtnsWrapper {
  align-self: center;
}
/* .landingBtnsWrapper button{
    background-color: #6D4031;
} */
.popupOpacity {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0px;
  left: 0px;
  z-index: 1200;
}

.pop-up-styling {
  padding: 10px;
  /* Shadow not working yet */
  box-shadow: 10px 10px 12px 12px rgba(109, 64, 49, 0.5);
  border: 2px solid black;
}

@media (max-width: 400px) {
  .popupOpacity {
    display: hidden;
  }
}
/* End landing page */
@import "./css/pagetransitions.css";

.group-header-image {
  width: 100%;
  height: 25vh;
  object-fit: cover;
}

.group-header-text {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px;
  position: absolute;
  color: white;
  border-radius: 0 0 50px 0;
}

/* ----------Chat room styles ------------- */
.ChatPageWrapper {
  position: relative;
}
.room {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 0;
  background-color: #f9f3f1;
  max-width: 50%;
}
#root > div > div.ChatPageWrapper > div > main > div {
  max-width: 1000px;
}

#root > div > div.ChatPageWrapper > div > main > div {
  padding: 0;
}
.heading {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 50px;
  border-radius: 5px;
  font-family: "DM Sans";
  color: rgb(70, 70, 70);
  font-size: 2rem;
  font-weight: 500;
  z-index: 1000;
  background-color: #f2e7e3;
  text-align: center;
}
.heading-text {
  padding: 10px;
}
.MuiContainer-root {
  padding: 0;
}
.heading {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  border-radius: 5px;
  font-family: "DM Sans";
  color: rgb(70, 70, 70);
  font-size: 2rem;
  font-weight: 500;
  z-index: 1000;
  background-color: #f2e7e3;
}

.chatContainer {
  overflow:scroll;
  height: calc(100vh - 114px);
}

.messages {
  padding: 0;
  background-color: #f9f3f1;
  display: flex;
  flex-direction: column;
}
/* @media (max-width: 550px) {
  .messages {
    height: 94vh;

  }
} */

.messageContainer {
  /* max-width: 70%; */
  margin: 8px;
}

.you {
  align-self: flex-end;
}

.you .msg {
  background-color: #00bfb6;
}

.other .sender {
  text-align: start;
}

.you .sender {
  text-align: end;
}

.other .msg {
  background-color: #c08672;
}

.other {
  justify-content: flex-start;
  align-content: flex-start;
  width: auto;
}


.sender {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.msg {
  display: inline-block;
  position: relative;
  border-radius: 10px;
  padding: 15px;

}
.msg span{ 
  word-break: break-word;
}

.msg-time {
  margin: 0 !important;
  text-align: end;
  font-size: small;
  color: rgb(70, 69, 69);
}

.messageInputs{
  /* position: fixed; */
  box-shadow: 1px 1px 10px rgba(0,0,0,.1);
  width: 100%;
  height: 50px;
  border-radius: 5px;
  font-family: "DM Sans";
  color: rgb(70, 70, 70);
  font-size: 2rem;
  font-weight: 500;
  /* z-index: 1000; */
  background-color: #F2E7E3;
  text-align: center;
}
.messageInputs input {
  height: 40px;
  width: 50%;
  border: none;
  background-color: #f9f3f1;
  transition: all 1s ease-in-out;
}
.messageInputs input:focus {
  outline: none;
  background-color: white;
}

.sender {
  font-weight: bold;
}

/* ----------End of chat room styles ------------- */
